import './styles/section1.css';
import { FormattedMessage } from 'react-intl';
import {ReactComponent as Figure} from './images/figure.svg';
import { useEffect, useState } from 'react';
import { gsap } from 'gsap'
import { useRef } from 'react'

export default function Section1() {
    const about1 = useRef(null);
    const about2 = useRef(null);
    const about3 = useRef(null);
    const about4 = useRef(null);
    const about5 = useRef(null);
    const circle1 = useRef(null);
    const circle2 = useRef(null);
    const circle3 = useRef(null);
    const circle4 = useRef(null);
    const circle5 = useRef(null);

    const [days, setDays] = useState(0);

    useEffect(() => {
        let now = new Date();
        const myStart = new Date("2022-07-23");
        let days = Math.floor((now - myStart) / 1000 / 60 / 60 / 24);
        setDays(days);
        
        gsap.from([about1.current, about2.current, about3.current, about4.current, about5.current], {
            scrollTrigger: {
                trigger: '.section1',
                start: '+700'
            },
            x: -20,
            opacity: 0,
            duration: 0.5,
            stagger: 0.2
        })
        gsap.to(circle1.current, {
            scrollTrigger: {
                trigger: '.section1',
                scrub: 1
            },
            y: +200
        })
        gsap.to(circle2.current, {
            scrollTrigger: {
                trigger: '.section1',
                scrub: 1
            },
            y: +500
        })
        gsap.to(circle3.current, {
            scrollTrigger: {
                trigger: '.section1',
                scrub: 1
            },
            y: +100
        })
        gsap.to(circle4.current, {
            scrollTrigger: {
                trigger: '.section1',
                scrub: 1
            },
            y: +500
        })
        gsap.to(circle5.current, {
            scrollTrigger: {
                trigger: '.section1',
                scrub: 1
            },
            y: +200
        })

    }, [])
    return(
        <article id='section1' className="section1">
            <div className="bg">
                <div className="figures">
                    <Figure />
                    <Figure />
                </div>
                <div className="circles">
                    <div ref={circle1} className="circle circle1"></div>
                    <div ref={circle2} className="circle circle2"></div>
                    <div ref={circle3} className="circle circle3"></div>
                    <div ref={circle4} className="circle circle4"></div>
                    <div ref={circle5} className="circle circle5"></div>
                </div>
            </div>
            <section className="about-me">
                <h2 className='title'>ABOUT ME. ABOUT ME. ABOUT ME. ABOUT ME. ABOUT ME. ABOUT ME. ABOUT ME.</h2>
                <div className="content">
                    <div ref={about1} className="info">
                        <p className="about"><FormattedMessage id='about1'/> </p>
                    </div>

                    <div ref={about2} className="info">
                        <p className="about"><FormattedMessage id='about2'/> {days} <FormattedMessage id='about2_2'/> </p>
                        <p className="descript"><FormattedMessage id='descript_about2' /></p>
                    </div>

                    <div ref={about3} className="info">
                        <p className="about"><FormattedMessage id='about3'/></p>
                    </div>
                    <div ref={about4} className="info">
                        <p className="about"><FormattedMessage id='about4'/></p>
                    </div>
                    <div ref={about5} className="info">
                        <p className="about"><FormattedMessage id='about5'/></p>
                    </div>
                </div>
            </section>
        </article>
    )
}