import './styles/section3.css';
import { FormattedMessage } from 'react-intl';
import { useEffect, useRef} from 'react'
import { gsap, Power3 } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

export default function Section3() {
    const work1 = useRef(null)
    const work2 = useRef(null)
    const work3 = useRef(null)
    const work4 = useRef(null)
    const work5 = useRef(null)
    
    useEffect(() => {
        let worksArr = [work1.current, work2.current, work3.current, work4.current, work5.current]
        worksArr.forEach((work) => {
            gsap.from(work, {
                scrollTrigger: {
                    trigger: work,
                },
                x: -100,
                opacity: 0,
                duration: 1,
                ease: Power3.easeOut()

            })
        })
    }, [])


    return(
        <article id='section3' className="section3">
            <section className="my-works">
                <h2 className='title'>MY WORKS. MY WORKS. MY WORKS. MY WORKS. MY WORKS. MY WORKS. MY WORKS.</h2>
                <div className="content">

                    <div ref={work1}  className="work1 work">
                        <a target='_blank' href='https://sr-popug.github.io/DiveSea/' className="img">
                            <img className='site' src={require('./images/divesea site.png')} alt="work" />
                        </a>
                        <div className="info">
                            <h3>NFT WEBsite</h3>
                            <p className="year">2023</p>
                        </div>
                    </div>

                    <div ref={work2} className="work2 work">
                        <div className="info">
                            <h3>Go Surf Site</h3>
                            <p className="year">2023</p>
                        </div>
                        <a target='_blank' href='https://sr-popug.github.io/GoSurf/' className="img">
                            <img className='site' src={require('./images/GOSURF.png')} alt="work" />
                        </a>
                    </div>

                    <div ref={work3}  className="work3 work">
                    <div className="info">
                            <h3>SOS Game</h3>
                            <p className="year">2023</p>
                        </div>
                        <a target='_blank' href='https://sr-popug.github.io/SOS/' className="img">
                            <img className='site' src={require('./images/SOS Game site.png')} alt="work" />
                        </a> 
                    </div>

                    <div ref={work4} className="work4 work">
                        <a target='_blank' href='https://sr-popug.github.io/Sneakers/' className="img">
                            <img className='site' src={require('./images/nike site.png')} alt="work" />
                        </a>
                        <div className="info">
                            <h3>Shop of sneakers</h3>
                            <p className="year">2023</p>
                        </div>
                    </div>

                    <div ref={work5} className="work5 work">
                        <a target='_blank' href='https://sr-popug.github.io/MNBN/' className="img">
                            <img className='site' src={require('./images/MNTN.png')} alt="work" />
                        </a>
                        <div className="info">
                            <h3>travel website</h3>
                            <p className="year">2022</p>
                        </div>
                    </div>

                </div>
                <button className='all'><a href="https://github.com/sr-popug?tab=repositories"><FormattedMessage id='button_works' /></a></button>

                
            </section>
            
        </article>
    )
}