import './styles/section2.css';
import { FormattedMessage } from 'react-intl';
import { useEffect } from 'react';
import { gsap } from 'gsap';

export default function Section2() {
    useEffect(() => {
        let skillsArr = document.querySelectorAll('.skill')
            gsap.from(skillsArr, {
                scrollTrigger: {
                    trigger: '.section2',
                    start: 'top center'
                },
                opacity: 0,
                duration: 0.3,
                stagger: 0.1
            })
    }, [])
    return(
        <article id='section2' className="section2">
            <section className="my-skills">
                <h2 className='title'>MY SKILLS. MY SKILLS. MY SKILLS. MY SKILLS. MY SKILLS. MY SKILLS. MY SKILLS.</h2>
                <div className="content">
                    <div className="row">
                        <div className="skill">HTML</div>
                        <div className="skill">JavaScript</div>
                    </div>
                    <div className="row">
                        <div className="skill">CSS</div>
                        <div className="skill">React JS</div>
                    </div>
                    <div className="row">
                        <div className="skill">GSAP</div>
                    </div>
                    <div className="row">
                        <div className="skill">Axios</div>
                        <div className="skill">TypeScript</div>
                        <div className="skill">React-Router</div>
                    </div>
                        
                    <div className="row">
                        <div className="skill">cross-browser layout</div>
                        <div className="skill">LESS</div>
                    </div>
                    <div className="row">
                        <div className="skill">NPM</div>
                        <div className="skill">SCSS</div>
                        <div className="skill">SEO</div>
                    </div>
                    <div className="row">
                        <div className="skill">Tailwind</div>
                    </div>
                    <div className="row">
                        <div className="skill">Redux</div>
                    </div>
                    <div className="row">
                        <div className="skill">Adaptive Layout</div>
                        <div className="skill">Next.js</div>
                    </div>
                </div>
                <div className="bottom"><FormattedMessage id='lot_more' /></div>
            </section>
        </article>
    )
}