import { useEffect, useState } from 'react'
import './styles/main.css'
import { FormattedMessage } from 'react-intl'
import { gsap, Back } from 'gsap'
import { useRef } from 'react'
import { SplitText } from 'gsap/SplitText'


export default function Main() {
    const h1_1 = useRef(null)
    const h1_2 = useRef(null)
    const h1_3 = useRef(null)
    const descript = useRef(null)

    const [seconds, setSeconds] = useState(0)

    useEffect(() => {
        setInterval(() => {
            setSeconds(prev => prev+1)
        }, 1000);
        gsap.from(descript.current, {
            opacity: 0,
            duration: .025,
            delay: 1,
            stagger: .025,
        })
        gsap.from([h1_1.current, h1_2.current, h1_3.current], {
            y: 80,
            opacity: 0,
            duration: 1,
            scale: 0,
            stagger: 0.1,
            rotationX: 180,
            transformOrigin: '0% 50% -50',
            ease: Back.easeOut()
        })
       
    }, [])
    

    return(
         <main>
           <div className="content">
                <div className="left">
                    <h1 ref={h1_1}><FormattedMessage  id='hi1'/></h1>
                    <h1 ref={h1_2}><FormattedMessage  id='hi2'/></h1>
                    <h1 ref={h1_3}><FormattedMessage  id='hi3'/></h1>
                    <p ref={descript} className="descript"><FormattedMessage id='descript_main' /></p>
                    
                </div>
                <div className="right">
                <p className="timer-text"><FormattedMessage id='timer' /> 
                    <span className='timer'> 
                        {("0" + Math.floor((seconds / 3600))).slice(-2)}:
                        {("0" + Math.floor((seconds / 60)) % 60).slice(-2)}:
                        {("0" + ((seconds)) % 60).slice(-2)}
                    
                    </span> 
                </p>
                </div>
            </div>
         </main>
    )
}