import './styles/header.css'
import { FormattedMessage } from 'react-intl'
import { LOCALES } from '../../i18n/locales'
import { gsap } from 'gsap';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';
export default function Header({currentLocale, handleChange}) {
gsap.registerPlugin(ScrollToPlugin)
    const languages = [
        { name: 'ENG', code: LOCALES.ENGLISH },
        { name: '', code: LOCALES.RUSSIAN },
      ]

    function scrollToAbout() {
        gsap.to(window, {duration: .3, scrollTo: '.section1'});
    }
    function scrollToWorks() {
        gsap.to(window, {duration: .3, scrollTo: '.section3'});
    }
 
 
    return(
         <header>
            <div className="line-scroll"></div>
            <div className="i"><FormattedMessage id='my_name' /></div>
            <div className="right">
                <nav>
                    <ul>
                        <li onClick={scrollToAbout}><FormattedMessage id='nav1' /></li>
                        <li onClick={scrollToWorks}><FormattedMessage id='nav2' /></li>
                        <li><a href="mailto:ilikechebyreki@gmail.com"><FormattedMessage id='nav3' /></a></li>
                    </ul>
                </nav>
                <select onChange={handleChange}>
                    {languages.map(({ name, code }) => (
                        <option  key={code} value={code}>
                            {name}
                        </option>
                    ))}
                </select>
            </div>
         </header>
    )
}