import Header from "../components/Header/Header";
import { IntlProvider} from 'react-intl'
import { LOCALES } from '../i18n/locales'
import { messages } from '../i18n/massages'
import { useState, useRef, useEffect } from "react";
import Main from "../components/Main/Main";
import Section1 from "../components/Section1/Section1";
import Section2 from "../components/Section2/Section2";
import Section3 from "../components/Section3/Section3";
import Footer from "../components/Footer/Footer";
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import {ScrollSmoother} from 'gsap/ScrollSmoother'
import {SplitText} from 'gsap/SplitText'
import {gsap}  from 'gsap'
gsap.registerPlugin(ScrollSmoother, ScrollTrigger, SplitText)
gsap.core.globals("ScrollTrigger", ScrollTrigger);
gsap.core.globals("SplitText", SplitText);



export default function HomePage() {
    function handleChange({target: { value } }) {
        setCurrentLocale(value)
        console.log(value)
    }
    const wrapper = useRef(null)
    const content = useRef(null)
    useEffect(() =>{
        ScrollSmoother.create({
            content: content.current,
            wrapper: wrapper.current,
            smooth: 1.5
          })
    }, [])
    const [currentLocale, setCurrentLocale] = useState(LOCALES.ENGLISH)
    
    return (
        <IntlProvider messages={messages[currentLocale]} locale={currentLocale} defaultLocale={LOCALES.ENGLISH}>
            <Header currentLocale={currentLocale} handleChange={handleChange}/>

            <div ref={wrapper}>
                <div ref={content}>
                    <Main />
                    <Section1 />
                    <Section2 />
                    <Section3 />
                    <Footer />
                </div>
            </div>
        </IntlProvider>
    )
}